			if(makeGaps) return;
			var close=currentQuote[field];
			var nextClose=nextQuote[field];
			if(typeof close!="undefined" && typeof nextClose=="undefined"){
				CIQ.ensureDefaults(nextQuote, {
					Close: close,
					Open: close,
					High: close,
					Low: close,
					Volume: 0,
					Adj_Close: currentQuote.Adj_Close
				});
			}
